"use client";

import React, { useEffect, useState } from "react";
import { usePathname } from "next/navigation";
import toast from "react-hot-toast";
import { FaTrashAlt, FaRegEye } from "react-icons/fa";
import { MdOutlineEdit } from "react-icons/md";
import Spinner from "@/components/Layout/Spinner";
import { IoCloseSharp } from "react-icons/io5";

interface ProductDashboardProps {
  products: {
    wsCode: number;
    name: string;
    salesPrice: number;
    mrp: number;
    packageSize: number;
    category: string | null;
    tags: string[];
    enabled: boolean;
  }[];
}

const ProductDashboard: React.FC<ProductDashboardProps> = ({ products }) => {
  const [productState, setProductState] = useState(products);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(5);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [deleteProductWsCode, setDeleteProductWsCode] = useState<number | null>(null);
  const [loading, setLoading] = useState(false);
  const pathname = usePathname();

  const indexOfLastProduct = currentPage * itemsPerPage;
  const indexOfFirstProduct = indexOfLastProduct - itemsPerPage;
  const currentProducts = productState?.slice(indexOfFirstProduct, indexOfLastProduct);
  const totalPages = Math.ceil(productState?.length / itemsPerPage);

  const handleDelete = async (wsCode: number) => {
    setLoading(true);
    try {
      const response = await fetch(`/api/admin/products/${wsCode}`, {
        method: "DELETE",
      });
      const data = await response.json();

      if (response.ok) {
        setProductState(productState.filter((product) => product.wsCode !== wsCode));
        toast.success(data.message);
        setIsDeleteModalOpen(false);
      } else {
        toast.error(data.message || "Failed to delete product.");
      }
    } catch (error) {
      console.error("Error deleting product:", error);
      toast.error("An error occurred while deleting the product.");
    } finally {
      setLoading(false);
    }
  };

  const handleToggleStatus = async (wsCode: number, newStatus: boolean) => {
    try {
      const response = await fetch(`/api/admin/products/${wsCode}/toggle`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ enabled: !newStatus }),
      });

      if (response.ok) {
        toast.success(`Product ${newStatus ? "Disabled" : "Enabled"} Successfully`);
        setProductState((prev) =>
          prev.map((product) =>
            product.wsCode === wsCode ? { ...product, enabled: !product.enabled } : product
          )
        );
      } else {
        toast.error("Failed to toggle product status.");
      }
    } catch (error) {
      console.error("Error toggling product status:", error);
      toast.error("An error occurred while toggling product status.");
    }
  };

  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

  return (
    <div>
      <dialog className={isDeleteModalOpen ? "modal modal-open" : "modal"}>
        <div className="modal-box">
          <form method="dialog">
            <button
              className="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"
              aria-label="Close"
              onClick={() => setIsDeleteModalOpen(false)}
            >
              <IoCloseSharp />
            </button>
          </form>
          <h2 className="text-lg font-bold">Confirm Deletion</h2>
          <p>Are you sure you want to delete this product?</p>
          <div className="mt-4 flex justify-end">
            <button
              className="btn btn-primary hover:btn-error hover:text-white"
              onClick={() => deleteProductWsCode && handleDelete(deleteProductWsCode)}
            >
              {loading ? <Spinner /> : "Delete"}
            </button>
          </div>
        </div>
      </dialog>

      <div className="m-5 rounded-md bg-slate-100 p-5">
        <h1 className="text-xl font-bold">Product Management</h1>
        <p className="my-5 text-sm">
          Manage your products effectively by adding, editing, or deleting them as needed.
          You can also toggle product availability for better inventory control.
        </p>
        <table className="table table-auto shadow-md overflow-hidden rounded-lg bg-white w-full">
          <thead className="bg-slate-700 text-white">
            <tr>
              <th className="text-center">Sr No</th>
              <th className="text-center">Name</th>
              <th className="text-center">WS Code</th>
              <th className="text-center">Sales Price</th>
              <th className="text-center">MRP</th>
              <th className="text-center">Status</th>
              <th className="text-center">Actions</th>
            </tr>
          </thead>
          <tbody>
            {currentProducts?.map((product, index) => (
              <tr key={product.wsCode}>
                <td className="text-center">{indexOfFirstProduct + index + 1}</td>
                <td className="text-center">{product.name}</td>
                <td className="text-center">{product.wsCode}</td>
                <td className="text-center">{product.salesPrice}</td>
                <td className="text-center">{product.mrp}</td>
                <td className="text-center">
                  <span
                    className={`badge ${
                      product.enabled ? "badge-success" : "badge-error"
                    }`}
                  >
                    {product.enabled ? "Active" : "Inactive"}
                  </span>
                </td>
                <td className="text-center">
                  <a href={`${pathname}/${product.wsCode}`}>
                    <button className="btn btn-sm bg-blue-500 text-white hover:bg-blue-700 mr-2">
                      <MdOutlineEdit />
                    </button>
                  </a>
                  <button
                    className="btn btn-sm bg-red-500 text-white hover:bg-red-700 mr-2"
                    onClick={() => {
                      setIsDeleteModalOpen(true);
                      setDeleteProductWsCode(product.wsCode);
                    }}
                  >
                    <FaTrashAlt />
                  </button>
                  <button
                    className="btn btn-sm bg-green-500 text-white hover:bg-green-700"
                    onClick={() =>
                      handleToggleStatus(product.wsCode, product.enabled)
                    }
                  >
                    {product.enabled ? "Disable" : "Enable"}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <div className="flex justify-between items-center mt-4">
          <div>
            <button
              className="btn-sm mr-6 rounded-md bg-slate-700 text-white"
              onClick={() => paginate(currentPage - 1)}
              disabled={currentPage === 1}
            >
              Previous
            </button>
            <span>
              Page {currentPage} of {totalPages}
            </span>
            <button
              className="ml-6 btn-sm rounded-md bg-slate-700 text-white"
              onClick={() => paginate(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              Next
            </button>
          </div>
          <p className="font-bold">Total Products: {productState?.length}</p>
        </div>
      </div>
    </div>
  );
};

export default ProductDashboard;
