"use client";

import React, { useState } from "react";
import { AiOutlinePlusCircle } from "react-icons/ai";
import { IoCloseSharp } from "react-icons/io5";
import toast, { Toaster } from "react-hot-toast";

const ProductBuilder: React.FC = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [name, setName] = useState("");
  const [wsCode, setWsCode] = useState("");
  const [salesPrice, setSalesPrice] = useState("");
  const [mrp, setMrp] = useState("");
  const [packageSize, setPackageSize] = useState("");
  const [category, setCategory] = useState("");
  const [tags, setTags] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);

  const handleAddProduct = async () => {
    if (!name || !wsCode || !salesPrice || !mrp || !packageSize || !category) {
      toast.error("Please fill all required fields!");
      return;
    }

    setLoading(true);

    try {
      const response = await fetch("/api/admin/products", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name,
          wsCode,
          salesPrice: parseFloat(salesPrice),
          mrp: parseFloat(mrp),
          packageSize: parseFloat(packageSize),
          tags,
          category,
        }),
      });

      const data = await response.json();
      if (data.success) {
        toast.success("Product added successfully!");
        setIsModalOpen(false);
        resetForm();
      } else {
        toast.error(data.message || "Failed to add product");
      }
    } catch (error) {
      console.error("Error adding product:", error);
      toast.error("An error occurred.");
    } finally {
      setLoading(false);
    }
  };

  const resetForm = () => {
    setName("");
    setWsCode("");
    setSalesPrice("");
    setMrp("");
    setPackageSize("");
    setCategory("");
    setTags([]);
  };

  return (
    <div className="max-w-4xl mx-auto">
      <Toaster position="top-center" reverseOrder={false} />
      <button
        className="flex items-center justify-center w-full p-6 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-xl shadow-md hover:shadow-lg transition duration-300"
        onClick={() => setIsModalOpen(true)}
      >
        <AiOutlinePlusCircle size={30} className="mr-3" />
        Add New Product
      </button>

      <dialog className={`modal ${isModalOpen ? "modal-open" : ""}`}>
        <div className="modal-box rounded-lg">
          <form
            onSubmit={(e) => {
              e.preventDefault();
              handleAddProduct();
            }}
          >
            <button
              type="button"
              className="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"
              onClick={() => setIsModalOpen(false)}
            >
              <IoCloseSharp />
            </button>
            <h2 className="text-2xl font-bold text-center mb-6">Add New Product</h2>
            <div className="space-y-4">
              <input
                type="text"
                placeholder="Product Name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="input input-bordered w-full"
              />
              <input
                type="number"
                placeholder="WS Code (Unique)"
                value={wsCode}
                onChange={(e) => setWsCode(e.target.value)}
                className="input input-bordered w-full"
              />
              <input
                type="number"
                placeholder="Sales Price"
                value={salesPrice}
                onChange={(e) => setSalesPrice(e.target.value)}
                className="input input-bordered w-full"
              />
              <input
                type="number"
                placeholder="MRP"
                value={mrp}
                onChange={(e) => setMrp(e.target.value)}
                className="input input-bordered w-full"
              />
              <input
                type="number"
                placeholder="Package Size"
                value={packageSize}
                onChange={(e) => setPackageSize(e.target.value)}
                className="input input-bordered w-full"
              />
              <select
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                className="select select-bordered w-full"
              >
                <option value="">Select Category</option>
                <option value="Category1">Category 1</option>
                <option value="Category2">Category 2</option>
              </select>
              <textarea
                placeholder="Tags (comma-separated)"
                value={tags.join(", ")}
                onChange={(e) => setTags(e.target.value.split(",").map((tag) => tag.trim()))}
                className="textarea textarea-bordered w-full"
              ></textarea>
            </div>
            <div className="mt-6 flex justify-end">
              <button
                type="submit"
                className="btn btn-primary w-full"
                disabled={loading}
              >
                {loading ? "Adding..." : "Add Product"}
              </button>
            </div>
          </form>
        </div>
      </dialog>
    </div>
  );
};

export default ProductBuilder;
